<template>
  <div class="courseDetail">
    <div class="mescroll" id="list">
      <div id="listData">
        <div class="top">
          <div class="title">
            <img :src="detail.shopHeader?$utils.dealImg(detail.shopHeader):require('../../assets/img/default/userDefault.png')" alt />
            <p>{{detail.shopName}}</p>
          </div>
          <img class="banner" :src="detail.aimg?$utils.dealImg(detail.aimg):require('@/assets/img/default/defaultH.png')" alt />
          <div class="content">
            <div class="flex">
              <p class="name">{{detail.viewName}}</p>
              <img @click="toCollect" src="../../assets//img/merchantsIcon/collect.png" alt="">
            </div>
            <p class="text">
              {{detail.ades}}
            </p>
            <div class="flex">
              <div class="text1">{{detail.aoutPrice?`${detail.asaleNum}人开通`:`${detail.avisitNum}人浏览`}}</div>
              <div class="text2">{{detail.aoutPrice?'￥'+detail.aoutPrice.toFixed(2):'免费'}}</div>
            </div>
          </div>
        </div>
        <div class="mid">
          <van-tabs v-model="active" :line-width="20" :line-height="3" :sticky="true" :swipe-threshold="6" :border="false" :title-active-color="'#000'" @click="onClick">
            <van-tab title="详情">
              <div class="detail">
                <div v-html="detail.amsg"></div>
              </div>
            </van-tab>
            <van-tab title="目录">
              <div class="directory">
                <div class="list">
                  <div class="item" v-for="(item,index) in directoryList" :key="index" @click="goDirectoryDetail(item)">
                    <!-- <div class="left">
                    <p class="label">直播</p>
                  </div> -->
                    <div class="right">
                      <div class="name">{{item.dname}}</div>
                      <div class="bottom">
                        <div class="text1">{{item.updateTime}}</div>
                        <div class="text2">{{item.doutPrice?`${item.dsaleNum}人开通`:`${item.dvisitNum}人浏览`}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </van-tab>
            <van-tab title="相关推荐">
              <div class="recommend">
                <p class="title">精品课程</p>
                <div class="list">
                  <div class="item" v-for="(item,index) in recommendList" :key="index" @click="goRecommendDetail(item)">
                    <div class="left">
                      <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
                    </div>
                    <div class="right">
                      <p class="name">{{item.viewName}}</p>
                      <div class="bottom">
                        <div class="text1">{{item.aoutPrice?`${item.asaleNum}人开通`:`${item.avisitNum}人浏览`}}</div>
                        <div class="text2">￥{{item.aoutPrice}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </van-tab>
          </van-tabs>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import { Tab, Tabs } from "vant";
import { getCourseDetail, getDirectoryList, getRecommendList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      active: 0,
      acode: "",
      detail: {},
      mescroll: null,
      directoryList: [],
      recommendList: []
    };
  },
  methods: {
    onClick() {
      if (this.active > 0) {
        if (this.mescroll) {
          this.mescroll.scrollTo(0, 0);
          this.mescroll.resetUpScroll();
        } else {
          this.$nextTick(() => {
            this.startMescroll();
          });
        }
      } else {
        this.mescroll.destroy();
        this.mescroll = null;
      }
    },
    goDirectoryDetail(item) {
      sessionStorage.setItem("directoryId", item.dcode);
      this.$router.push({
        name: "videoDetail"
      });
    },
    goRecommendDetail(item) {
      sessionStorage.setItem("acode", item.acode);
      this.active = 0;
      this.mescroll.destroy();
      this.mescroll = null;
      this.acode = sessionStorage.getItem("acode");
      this.loadData();
    },
    toCollect() {
      this.$router.push({
        name: "collectList"
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getCourseDetail({ acode: this.acode }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
          this.detail.amsg = this.detail.amsg.replace(
            /(<img[\s\S]+?)src=(['"])/g,
            '<img src="' + this.$config.http
          );
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      });
    },
    getList(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      console.log();
      let dataObj = {
        acode: this.acode,
        pageNum: pageNum,
        pageSize: pageSize
      };
      if (this.active == 1) {
        getDirectoryList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data, "directoryList");
            } else {
              $this.$toast.fail(res.errorMsg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else if (this.active == 2) {
        getRecommendList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data, "recommendList");
            } else {
              $this.$toast.fail(res.errorMsg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else {
        console.log(111);
      }
    },
    startMescroll() {
      this.mescroll = new MeScroll(`list`, {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: `listData`,
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.getList(
        page.num,
        page.size,
        function(curPageData, name) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData, name);
          if (page.num == 1) $this[name] = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this[name].push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    this.acode = sessionStorage.getItem("acode");
    if (sessionStorage.getItem("active")) {
      console.log(sessionStorage.getItem("active"));
      this.active = Number(sessionStorage.getItem("active"));
      this.$nextTick(() => {
        this.startMescroll();
      });
      sessionStorage.removeItem("active");
    }
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.courseDetail {
  background: #fff;
  height: 100vh;
  .top {
    .title {
      display: flex;
      align-items: center;
      padding: 10px;
      background: #fff;
      img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 5px;
      }
      p {
        font-size: 13px;
        line-height: 20px;
      }
    }
    .banner {
      width: 100vw;
      display: block;
    }
    .content {
      background: #fff;
      padding: 10px;
      .name {
        font-size: 15px;
        font-weight: bold;
        flex: 2;
        line-height: 25px;
      }
      img {
        width: 30px;
      }
      .text {
        font-size: 14px;
        color: #999;
        line-height: 20px;
      }
      .flex {
        margin: 5px 0 0;
        display: flex;
        align-items: center;
        .text1 {
          color: #999;
          font-size: 13px;
          flex: 2;
        }
        .text2 {
          flex: 1;
          text-align: right;
          color: #9e1435;
          font-size: 13px;
        }
      }
    }
  }
  .mid {
    background: #fff;
    .detail {
      padding: 10px;
      img {
        width: 100%;
      }
    }
    .directory {
      padding: 10px;
      .list {
        .item {
          margin-bottom: 10px;
          display: flex;
          padding: 10px 0;
          border-bottom: 1pt solid #efefef;
          .left {
            width: 60px;
            margin-right: 10px;
            .label {
              border: 1pt solid #efefef;
              text-align: center;
              font-size: 13px;
              line-height: 25px;
              border-radius: 3px;
            }
          }
          .right {
            flex: 2;
            .name {
              line-height: 25px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .bottom {
              display: flex;
              align-items: center;
              margin-top: 5px;
              .text1 {
                color: #999;
                font-size: 13px;
                flex: 2;
              }
              .text2 {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
    .recommend {
      padding: 10px;
      .title {
        font-weight: bold;
        border-left: 2px solid #f00;
        padding-left: 10px;
        line-height: 20px;
        margin: 5px 0;
      }
      .list {
        .item {
          border-bottom: 1pt solid #efefef;
          margin-bottom: 10px;
          padding: 5px 0;
          display: flex;
          .left {
            img {
              width: 120px;
              margin-right: 5px;
            }
          }
          .right {
            position: relative;
            flex: 2;
            .name {
              font-size: 13px;
              line-height: 20px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .bottom {
              position: absolute;
              bottom: 0;
              left: 0;
              display: flex;
              width: 100%;
              line-height: 30px;
              .text1 {
                color: #999;
                font-size: 13px;
                flex: 2;
              }
              .text2 {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
<style lang="scss">
.courseDetail {
  img {
    width: 100%;
  }
  .mescroll-empty {
    transform: translateY(-5%);
    img {
      width: 120px;
    }
  }
  .van-tabs__wrap {
    border-bottom: 1pt solid #e0e0e0;
  }
}
</style>
